body {
  font-family: 'Inter', sans-serif;

  font-size: 18px;
  line-height: 136%;

  color: #000;
}

.container {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;

  margin: 0 auto;
}

.page {
  &-wrapper {
    display: flex;
    flex-direction: column;

    min-height: 100vh;

    &__header, &__footer {
      flex: 0 0 auto;

      background-color: #fff;
    }

    &__content {
      flex: 1 0 auto;

      padding: 80px 0;

      background-color: #F9F9F9;
    }
  }

  &-content {
    &__header {
      position: relative;
      &-block {
        & + & {
          margin-top: 40px;
        }
      }

      &-back {
        position: absolute;
        left: 0;

        top: -60px;
      }

    }

    &__title {
      font-size: 30px;
      font-weight: 600;
      line-height: 36px;

      color: $monoBlack;
    }
  }

  &-tabs {
    &__nav {
      display: flex;
      align-items: center;

      &-item {
        width: 100%;
        max-width: 150px;
        height: 58px;

        color: $secondary;

        border-bottom: 2px solid transparent;

        transition: .5s border-bottom-color;

        &.active {
          color: $accent;

          border-bottom-color: $accent;

          &:hover {
            color: $accentHover;

            border-bottom-color: $accentHover;
          }
        }
      }

      &-link {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 100%;

        font-size: 17px;
        font-weight: 600;
        color: inherit;

        transition: .5s color;

        outline: none;
      }
    }

    &__content {
      padding-top: 40px;
    }
  }
}

.icon {
  width: 100%;
  height: 100%;
}

.input {
  &__item {
    -webkit-appearance: none;
    border: none;
    background: none;
    outline: none;

    width: 100%;
    height: 100%;

    padding: 0 16px;
    font-size: 18px;
    color: $monoBlack;
  }
}


.tabs {
  &-content {
    &__item {
      display: none;

      &.active {
        display: block;
      }
    }
  }
}

.btn {
  -webkit-appearance: none;

  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  &-accent {
    background-color: $accent;

    font-size: 16px;
    font-weight: 500;
    color: #fff;

    border-radius: 4px;

    transition: .5s background-color;

    &:hover {
      background-color: $accentHover;
    }

    &-light {
      background-color: $accentSecondary;

      font-size: 17px;
      font-weight: 500;
      color: $accent;

      border-radius: 4px;

      transition: .5s background-color;
    }
  }
}

.back-link {
  width: 100px;
  height: 40px;
}


.link {
  color: $accent;

  transition: .5s color;

  &:hover {
    color: $accentHover;
  }
}

@media screen and (max-width: 500px) {
  .page {
    &-tabs {
      &__nav {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
      }
    }
  }
}