.certificate{
  &-card{
    &__wrapper{
      padding: 30px;

      max-width: 670px;
      width: 100%;


      background: #FFFFFF;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
      border-radius: 20px;
    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;
    }
    &__name{
      margin-left: 15px;

      font-size: 21px;
      line-height: 24px;
      letter-spacing: -0.2px;
      font-weight: 600;

      color:$monoBlack;
    }
    &__content{
      margin-top: 15px;

      display: flex;
      align-items: center;
    }
    &__footer{
      margin-top: 25px;
    }
    &__btn{
      max-width: 200px;
      height: 40px;
    }
  }
  &-info{
    &__title{
      color:$accent;

      text-align: center;
    }
    &__date{
      margin-top: 15px;

    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;

      text-align: center;

    }
    &__download{
      margin-top: 40px;

      height: 60px;
    }
    &__share{
      margin-top: 20px;
      &-list{
        margin-top: 20px;

        display: flex;
        align-items: center;
        justify-content: center;
      }
      &-item{
        & + &{
          margin-left: 15px;
        }
      }
      &-icon{
        width: 36px;
        height: 36px;

        display: block;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
    &__btn{
      margin-top: 40px;

      height: 60px;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 275px auto;
    gap: 125px;
  }
}


@media screen and (max-width: 600px) {
  .certificate{
    &-card{
      &__content{
        align-items: flex-start;
        flex-direction: column;
      }
      &__name{
        margin: 15px 0 0 0;
      }
    }
  }
}