.filter{
  &-wrapper{
    display: flex;
    align-items: center;
    &__search{
      width: 100%;
      max-width: 370px;
      height: 55px;
      &-input{
        -webkit-appearance: none;

        width: 100%;
        height: 100%;

        background: #EBEBEB;
        outline: none;
        border: none;
        border-radius: 10px;

        padding: 0 25px;

        color:$monoBlack;
        font-size: 18px;
      }
    }
    &__category{
      margin-left: 30px;
    }
  }
  &-category{
    &__list{
      display: flex;
      align-items: center;
      &-item{
        display: flex;
        align-items: center;

        color:$monoBlack;
        &::after{
          content: '';
          width: 12px;
          height: 8px;
          display: block;

          background: url("../img/arrow-black.svg") center center/contain no-repeat;
          margin-left: 10px;
        }
        & + &{
          margin-left: 30px;
        }
      }
      &-link{
        font-size: 17px;
        font-weight: 600;
        color:inherit;
      }
    }
  }
}

@media screen and (max-width: 1110px) {
  .filter{
    &-wrapper{
      flex-direction: column;
      align-items: flex-start;
      &__category{
        margin: 30px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .filter{
    &-category{
      &__list{
        flex-direction: column;
        align-items: flex-start;
        &-item{
          & + &{
            margin: 30px 0 0 0;
          }
        }
      }
    }
  }
}