.header{
  &__wrapper{
    height: 110px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    background-color: #fff;
  }
  &__block{
    max-width: 228px;
    width: 100%;
  }
  &__logo{
    height: 60px;
    &-img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__profile{
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &-block + &-block{
      margin-left: 50px;
    }
  }
  &__notification{
    position: relative;
  }
  &-profile{
    &__link{
      display: flex;
      align-items: center;

      &:hover{
        cursor: pointer;
      }
      &-icon{
        display: block;

        margin-left: 17px;

        svg{
          width: 14px;
          height: 8px;
        }
      }
    }
    &__img{
      width: 48px;
      height: 48px;

      border-radius: 50%;
      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
  }
  &-list{
    &__item + &__item{
      margin-left: 30px;
    }
    &__link{
      font-size: 17px;
      font-weight: 600;

      color: $monoBlack;
    }
  }
}


.notification{
  &-link{
    width: 30px;
    height: 35px;

    position: relative;
    &__count{
      position: absolute;
      right: -4px;
      top: 2px;

      width: 17px;
      height: 17px;

      display:flex;
      align-items: center;
      justify-content: center;

      border-radius: 50%;

      background-color: $accent;

      font-size: 10px;
      color:#fff;
      letter-spacing: -0.306546px;

      transition: .5s background-color;
      &:hover{
        background-color: $accentHover;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header{
    &__nav{
      display: none;
    }
  }
}


@media screen and (max-width: 600px) {
  .header{
    &__notification{
      display: none;
    }
  }
}