*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-Regular/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter/Inter-Regular/Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-Medium/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter/Inter-Medium/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-SemiBold/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter/Inter-SemiBold/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  line-height: 136%;
  color: #000;
}

.container {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;
  margin: 0 auto;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page-wrapper__header, .page-wrapper__footer {
  flex: 0 0 auto;
  background-color: #fff;
}

.page-wrapper__content {
  flex: 1 0 auto;
  padding: 80px 0;
  background-color: #F9F9F9;
}

.page-content__header {
  position: relative;
}

.page-content__header-block + .page-content__header-block {
  margin-top: 40px;
}

.page-content__header-back {
  position: absolute;
  left: 0;
  top: -60px;
}

.page-content__title {
  font-size: 30px;
  font-weight: 600;
  line-height: 36px;
  color: #020203;
}

.page-tabs__nav {
  display: flex;
  align-items: center;
}

.page-tabs__nav-item {
  width: 100%;
  max-width: 150px;
  height: 58px;
  color: #95929C;
  border-bottom: 2px solid transparent;
  transition: .5s border-bottom-color;
}

.page-tabs__nav-item.active {
  color: #FF7940;
  border-bottom-color: #FF7940;
}

.page-tabs__nav-item.active:hover {
  color: #FF8E5F;
  border-bottom-color: #FF8E5F;
}

.page-tabs__nav-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 17px;
  font-weight: 600;
  color: inherit;
  transition: .5s color;
  outline: none;
}

.page-tabs__content {
  padding-top: 40px;
}

.icon {
  width: 100%;
  height: 100%;
}

.input__item {
  -webkit-appearance: none;
  border: none;
  background: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 0 16px;
  font-size: 18px;
  color: #020203;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active {
  display: block;
}

.btn {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.btn-accent {
  background-color: #FF7940;
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  border-radius: 4px;
  transition: .5s background-color;
}

.btn-accent:hover {
  background-color: #FF8E5F;
}

.btn-accent-light {
  background-color: #FFEEE6;
  font-size: 17px;
  font-weight: 500;
  color: #FF7940;
  border-radius: 4px;
  transition: .5s background-color;
}

.back-link {
  width: 100px;
  height: 40px;
}

.link {
  color: #FF7940;
  transition: .5s color;
}

.link:hover {
  color: #FF8E5F;
}

@media screen and (max-width: 500px) {
  .page-tabs__nav {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
  }
}

.header__wrapper {
  height: 110px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
}

.header__block {
  max-width: 228px;
  width: 100%;
}

.header__logo {
  height: 60px;
}

.header__logo-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__profile {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header__profile-block + .header__profile-block {
  margin-left: 50px;
}

.header__notification {
  position: relative;
}

.header-profile__link {
  display: flex;
  align-items: center;
}

.header-profile__link:hover {
  cursor: pointer;
}

.header-profile__link-icon {
  display: block;
  margin-left: 17px;
}

.header-profile__link-icon svg {
  width: 14px;
  height: 8px;
}

.header-profile__img {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  overflow: hidden;
}

.header-profile__img-item {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-list__item + .header-list__item {
  margin-left: 30px;
}

.header-list__link {
  font-size: 17px;
  font-weight: 600;
  color: #020203;
}

.notification-link {
  width: 30px;
  height: 35px;
  position: relative;
}

.notification-link__count {
  position: absolute;
  right: -4px;
  top: 2px;
  width: 17px;
  height: 17px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #FF7940;
  font-size: 10px;
  color: #fff;
  letter-spacing: -0.306546px;
  transition: .5s background-color;
}

.notification-link__count:hover {
  background-color: #FF8E5F;
}

@media screen and (max-width: 991px) {
  .header__nav {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .header__notification {
    display: none;
  }
}

.footer__wrapper {
  background: #fff;
  padding: 40px 0 35px 0;
}

.footer__row {
  display: flex;
  justify-content: space-between;
}

.footer__row + .footer__row {
  margin-top: 45px;
}

.footer__logo {
  height: 60px;
  max-width: 228px;
  width: 100%;
}

.footer__logo-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer__copyright {
  color: #020203;
  font-size: 18px;
}

.footer__link {
  font-weight: 600;
  font-size: 17px;
  color: #FF7940;
  text-align: right;
  transition: .5s color;
}

.footer__link:hover {
  color: #FF8E5F;
}

.footer-social__text {
  color: #000;
  font-size: 18px;
}

.footer-social__list {
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer-social__list-item + .footer-social__list-item {
  margin-left: 12px;
}

.footer-social__list-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #BABABA;
}

.footer-social__list-icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 767px) {
  .footer__row {
    align-items: center;
  }
  .footer-social__list {
    margin-top: 0;
  }
  .footer-social__text {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .footer__logo {
    height: 40px;
  }
  .footer__wrapper {
    padding: 25px 0 35px 0;
  }
  .footer__row {
    flex-direction: column;
  }
  .footer__row + .footer__row {
    margin-top: 20px;
  }
  .footer__block + .footer__block {
    margin-top: 20px;
  }
}

.profile-info__wrapper {
  display: flex;
  align-items: center;
}

.profile-info__img {
  width: 75px;
  height: 75px;
  flex-shrink: 0;
  border-radius: 50%;
  overflow: hidden;
}

.profile-info__img-item {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-info__content {
  margin-left: 15px;
}

.profile-info__text {
  font-weight: 600;
  font-size: 25px;
  line-height: 30px;
  letter-spacing: -0.2px;
  color: #071013;
  max-width: 280px;
  word-break: break-word;
}

.profile-content__block + .profile-content__block {
  margin-top: 30px;
}

.profile-content__text {
  font-size: 18px;
  line-height: 24px;
  color: #95929C;
}

.profile-content__text-link {
  color: #FF7940;
  transition: .5s color;
}

.profile-content__text-link:hover {
  color: #FF8E5F;
}

.profile-content__input {
  max-width: 470px;
  height: 60px;
  background: #FFFFFF;
  border: 1px solid #D2D2D2;
  border-radius: 5px;
}

@media screen and (max-width: 600px) {
  .profile-content__text_center-mobile {
    text-align: center;
  }
}

.certificate-card__wrapper {
  padding: 30px;
  max-width: 670px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
}

.certificate-card__text {
  font-size: 18px;
  line-height: 24px;
  color: #95929C;
}

.certificate-card__name {
  margin-left: 15px;
  font-size: 21px;
  line-height: 24px;
  letter-spacing: -0.2px;
  font-weight: 600;
  color: #020203;
}

.certificate-card__content {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.certificate-card__footer {
  margin-top: 25px;
}

.certificate-card__btn {
  max-width: 200px;
  height: 40px;
}

.certificate-info__title {
  color: #FF7940;
  text-align: center;
}

.certificate-info__date {
  margin-top: 15px;
}

.certificate-info__text {
  font-size: 18px;
  line-height: 24px;
  color: #95929C;
  text-align: center;
}

.certificate-info__download {
  margin-top: 40px;
  height: 60px;
}

.certificate-info__share {
  margin-top: 20px;
}

.certificate-info__share-list {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.certificate-info__share-item + .certificate-info__share-item {
  margin-left: 15px;
}

.certificate-info__share-icon {
  width: 36px;
  height: 36px;
  display: block;
}

.certificate-info__share-icon svg {
  width: 100%;
  height: 100%;
}

.certificate-info__btn {
  margin-top: 40px;
  height: 60px;
}

.certificate__grid {
  display: grid;
  grid-template-columns: 275px auto;
  gap: 125px;
}

@media screen and (max-width: 600px) {
  .certificate-card__content {
    align-items: flex-start;
    flex-direction: column;
  }
  .certificate-card__name {
    margin: 15px 0 0 0;
  }
}

.courses-empty {
  display: flex;
  align-items: center;
  justify-content: center;
}

.courses-empty__wrapper {
  max-width: 370px;
  width: 100%;
}

.courses-empty__img {
  width: 100px;
  height: 80px;
  margin: 0 auto;
}

.courses-empty__img-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.courses-empty__content {
  margin-top: 60px;
}

.courses-empty__text {
  font-size: 18px;
  line-height: 24px;
  color: #020203;
  text-align: center;
}

.courses-empty__footer {
  margin-top: 35px;
}

.courses-empty__btn {
  height: 60px;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: 580px;
  gap: 45px 30px;
}

.courses-card {
  height: 100%;
}

.courses-card__item {
  border-radius: 15px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: 100%;
}

.courses-card__header {
  height: 250px;
  position: relative;
}

.courses-card__start {
  position: absolute;
  top: 20px;
  right: 20px;
  display: block;
  padding: 6px 9px;
  background-color: #FF7940;
  border-radius: 4px;
  font-weight: 600;
  font-size: 17px;
  color: #FFFFFF;
}

.courses-card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.courses-card__wrapper {
  padding: 30px 20px 20px 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.courses-card__title {
  font-weight: 600;
  font-size: 25px;
  line-height: 30px;
  letter-spacing: -0.2px;
  color: #000000;
}

.courses-card__text {
  font-size: 18px;
  line-height: 24px;
  color: #020203;
  margin-top: 10px;
}

.courses-card__footer {
  padding-top: 15px;
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.courses-card__btn {
  height: 40px;
  max-width: 150px;
}

.courses-card__time {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  font-weight: 600;
  font-size: 17px;
  color: #95929C;
}

.courses-card__time::before {
  content: '';
  width: 24px;
  height: 24px;
  display: block;
  background: url("../img/clock.svg") center center/contain no-repeat;
  margin-right: 8px;
}

@media screen and (max-width: 1050px) {
  .courses-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media screen and (max-width: 767px) {
  .courses-grid {
    grid-template-columns: minmax(0, 1fr);
    grid-auto-rows: auto;
  }
  .courses-card__header {
    height: 160px;
  }
  .courses-card__footer {
    margin-top: 30px;
    padding: 0;
  }
}

.filter-wrapper {
  display: flex;
  align-items: center;
}

.filter-wrapper__search {
  width: 100%;
  max-width: 370px;
  height: 55px;
}

.filter-wrapper__search-input {
  -webkit-appearance: none;
  width: 100%;
  height: 100%;
  background: #EBEBEB;
  outline: none;
  border: none;
  border-radius: 10px;
  padding: 0 25px;
  color: #020203;
  font-size: 18px;
}

.filter-wrapper__category {
  margin-left: 30px;
}

.filter-category__list {
  display: flex;
  align-items: center;
}

.filter-category__list-item {
  display: flex;
  align-items: center;
  color: #020203;
}

.filter-category__list-item::after {
  content: '';
  width: 12px;
  height: 8px;
  display: block;
  background: url("../img/arrow-black.svg") center center/contain no-repeat;
  margin-left: 10px;
}

.filter-category__list-item + .filter-category__list-item {
  margin-left: 30px;
}

.filter-category__list-link {
  font-size: 17px;
  font-weight: 600;
  color: inherit;
}

@media screen and (max-width: 1110px) {
  .filter-wrapper {
    flex-direction: column;
    align-items: flex-start;
  }
  .filter-wrapper__category {
    margin: 30px 0 0 0;
  }
}

@media screen and (max-width: 650px) {
  .filter-category__list {
    flex-direction: column;
    align-items: flex-start;
  }
  .filter-category__list-item + .filter-category__list-item {
    margin: 30px 0 0 0;
  }
}

.payment-succes {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.payment-succes__wrapper {
  max-width: 370px;
  width: 100%;
}

.payment-succes__title {
  text-align: center;
}

.payment-succes__img {
  width: 100px;
  height: 100px;
  margin: 50px auto 0 auto;
}

.payment-succes__img-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.payment-succes__text {
  margin-top: 50px;
  font-size: 18px;
  line-height: 24px;
  color: #020203;
  text-align: center;
}

.payment-succes__btn {
  margin-top: 35px;
  height: 60px;
}
